
import api from '../api'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import value2name from 'vux/src/filters/value2name.js'

export default {
    name: "order_confirm",
    data: function() {
        return {
            step: 0,
            popup_address: false,
            addressData: ChinaAddressData,
            ship_name: '',
            ship_mobile: '1850300XXXX',
            ship_addr: "XX",
            ship_area: ["", "", ""], //
            order_data: [{
                name: ")",
                spec_info: ':1710~1920/1920~2170/2200~2500/2500~2690',
                spec_info1: ':11.5/11.5/12/13 dBi',
                spec_info2: ':0/0/0 ',
                s_url: '/static/grentech/201611071844501875.jpg',
                ordernum: 1
            }],
            cart_data: [],
            memo: '',
            address_data: {
                ship_name: '',
                ship_mobile: '1850300XXXX',
                zip: '',
                ship_tel: '',
                ship_area: ['', '', ''],
                ship_addr: 'XX',
            },
        }
    },
    created: function() {
        //
        this.fetch_cart_data();
    },
    methods: {
        edit_address: function() {
            // console.log("click");
            this.popup_address = !this.popup_address;
        },
        fetch_cart_data: function() {
            var self = this;
            api.get_cart_data().then(res => {
                console.log(res);
                if (res.ok) {
                    if (res.data.data.length) {
                        self.cart_data = res.data.data;
                    } else {
                        self.cart_data = res.data.data;
                        self.$vux.toast.show({
                            text: '<span class="font-normal"></span>',
                            type: 'text'
                        });
                    }
                } else {

                }
            })
        },
        getName: function(val) {

            return value2name(val, ChinaAddressData)
        },
        submit_order_data: function() {
            var self = this;
            console.log(this.cart_data);
            console.log(this.memo);

            var cart_id = [];
            for (var i = 0; i < this.cart_data.length; i++) {
                cart_id.push(this.cart_data[i].cart_objects.id);
            }
            console.log(cart_id);
            //

            if (this.ship_area == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal"></span>',
                    type: 'warn'
                })
                return;
            }
            if (this.ship_name == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal"></span>',
                    type: 'warn'
                })
                return;
            }
            if (this.ship_mobile == "") {
                this.$vux.toast.show({
                    text: '<span class="font-normal"></span>',
                    type: 'warn'
                })
                return;
            }
            var order_data = {
                ship_area: self.ship_area.join("/"),
                ship_addr: self.address_data.ship_addr,
                ship_name: self.address_data.ship_name,
                ship_mobile: self.address_data.ship_mobile,
                addr_id: 0,
                memo: self.memo,
                //addr_id:self.address_data.ship_area,//
                cart_id: JSON.stringify({
                    cart_id: cart_id
                })
            }
            console.log(order_data);

            api.add_order(order_data).then(res => {
                if (res.ok) {
                    if (res.data.length) {
                        this.$vux.toast.show({
                            text: '<span class="font-normal">,</span>',
                            type: 'success'
                        })
                        setTimeout(function() {
                            self.$router.push("user");
                        }, 2000)

                    } else {
                        this.$vux.toast.show({
                            text: '<span class="font-normal">' + res.data.req + '</span>',
                            type: 'warn'
                        })
                    }
                }
                console.log(res);
            })
        },
        save_address_data: function() {
            var ship_area = $(".vux-popup-picker-value").text();
            console.log(ship_area);
            if (ship_area != "") {
                this.ship_area = ship_area.split(" ");
            }
            console.log(this.ship_area);
            this.popup_address = false;
        },
        cancel_address_edit: function() {
            this.popup_address = false;
        }
    },
    components: {
        Flexbox,
        FlexboxItem,
        Group,
        Cell,
        XButton,
        XTextarea,
        Tabbar,
        TabbarItem,
        XAddress,
        ChinaAddressData,
        Popup,
        XInput,
        Step,
        StepItem
    }
}



